<template>
	<view>
		<u-sticky bgColor="#fff">
			<u-tabs class='head' :list="list" :current='cur' @change="changeTab" :scrollable='false' :activeStyle="{
            color: '#303133',
            fontWeight: 'bold',
            transform: 'scale(1.05)'
        }"></u-tabs>
		</u-sticky>
		<view class="equipmentlist-ul-wrap" v-if="cur==0&&first_hexList">
			<view class="equipmentlist-ul" v-for="(item,index) in first_hexList" :key="item.id">
				<view class="equipment-item">
					<view class="item-top">
						<view class="pic" :style="'background-image:url('+item.imgUrl+')'">
						</view>
						<view class="name">{{item.name}}</view>
					</view>
					<view class="item-effect">
						<view class="part-title">效果</view>
						<view class="equipment-effect">{{item.description}}</view>
					</view>
				</view>
			</view>
		</view>

		<view class="equipmentlist-ul-wrap" v-if="cur==1&&second_hexList">
			<view class="equipmentlist-ul" v-for="(item,index) in second_hexList" :key="item.id">
				<view class="equipment-item">
					<view class="item-top">
						<view class="pic" :style="'background-image:url('+item.imgUrl+')'">
						</view>
						<view class="name">{{item.name}}</view>
					</view>
					<view class="item-effect">
						<view class="part-title">效果</view>
						<view class="equipment-effect">{{item.description}}</view>
					</view>
				</view>
			</view>
		</view>

		<view class="equipmentlist-ul-wrap" v-if="cur==2&&third_hexList">
			<view class="equipmentlist-ul" v-for="(item,index) in third_hexList" :key="item.id">
				<view class="equipment-item">
					<view class="item-top">
						<view class="pic" :style="'background-image:url('+item.imgUrl+')'">
						</view>
						<view class="name">{{item.name}}</view>
					</view>
					<view class="item-effect">
						<view class="part-title">效果</view>
						<view class="equipment-effect">{{item.description}}</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		options
	} from '@/common/options.js'
	import {
		_http
	} from '@/utils/request.js'
	import _ from 'lodash'
	export default {
		data() {
			return {
				list: [{
					name: '一级',
				}, {
					name: '二级',

				}, {
					name: '三级',

				}, ],
				cur: 0,
				hexList: {},
				first_hexList: {},
				second_hexList: {},
				third_hexList: {},
			};
		},
		methods: {
			changeTab(index) {
				this.cur = index.index
			}
		},


		onLoad() {
			//深拷贝
			function deepClone(obj) {
				let copy = obj instanceof Array ? [] : {}
				for (let key in obj) {
					if (obj.hasOwnProperty(key)) {
						copy[key] = typeof obj[key] === 'object' ? deepClone(obj[key]) : obj[key]
					}
				}
				return copy
			}
			this.hexList = deepClone(options.hexList)
			console.log(this.hexList);
			_.forEach(this.hexList, (v, k) => {
				if (v['type'] == '1') {
					this.first_hexList[k] = v;
				} else if (v['type'] == '2') {
					this.second_hexList[k] = v;
				} else {
					this.third_hexList[k] = v
				}
			})
		}
	}
</script>

<style lang="scss">
	.head {
		border-bottom: 1px solid #eff2f2;
	}

	.equipmentlist-ul-wrap {
		background-color: #f0f3f7;

		.equipmentlist-ul {

			.equipment-item {
				margin-bottom: 4vw;
				background-color: #ffffff;
				padding: 2vh 2vw;

				.item-top {
					display: flex;
					flex-direction: row;
					align-items: center;


					.pic {
						background-repeat: no-repeat;
						background-size: contain;
						width: 10vw;
						height: 10vw;
						background-color: #1c1c1c;
						margin-left: 4vw;
						border-radius: 1vw;
					}

					.name {
						margin-left: 4vw;
						font-size: 4vw;
					}
				}

				.item-effect {
					margin-left: 18vw;

					.part-title {
						font-size: 3vw;
					}

					.equipment-effect {
						font-size: 3vw;
						color: #757e7f;
					}
				}
			}
		}
	}
</style>
